// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Arkhip", sans-serif;
  --content-width: 1480px;
  --container-offset: 10px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --color-black: #000;
  --color-white: #fff;
  --greeny-blue: #46bd97;
  --brownish-grey: #666666;
  --charcoal-grey: #404041;
  --brownish-grey-43: rgba(102, 102, 102, 0.43);
  --almost-black: #090915;
  --light-grey: #f2efec;
  --very-green: #42f7bd;
  --very-blue: #3838b8;
  --color-sea: #fffef7;
}
