.snowmobiles {

	&__services {
		margin-top: 0px;
	}

  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    // width: 76%;
		// padding: 0;
		margin-top: 35px;
		
  }

  &__promotext {
    position: relative;
  	margin-top: 83px;
    width: 60%;
		align-self: start;

		@include mobile {
			margin-top: 20px;
		}
  }

  &__promo {
    // width: 42%;
		position: relative;
		margin-top: 100px;
		@include mobile {
			margin-top: 20px;
		}

    &::after {
      z-index: 100;
      position: absolute;
      content: "";
      left: -959px;
      bottom: 15px;
      height: 100px;
      width: 240px;
      background-image: url(../img/wave.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 97%;
      object-fit: cover;
    }
  }

  &__lead {
		display: inline-block;
    text-align: left;
		font-size: 24px;
		margin-bottom: 39px;
		@include mobile {
			font-size: 20px;
			margin-bottom: 15px;
		}
  }

  &__img {
		
		display: block;
		position: relative;
  }

  &__slogan {
    position: relative;
    display: block;
    width: 83%;
    font-family: "Arkhip", sans-serif;
    font-weight: normal;
    font-size: 54px;
    text-align: left;
    letter-spacing: -1.87px;
		color: var(--color-black);
		@include mobile {
			font-size: 25px;
		}

    &::after {
      z-index: 100;
      position: absolute;
      content: "";
      left: -95px;
      top: -11px;
      height: 100px;
      width: 200px;
      background-image: url(../img/wave.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 37%;
			object-fit: cover;
			@include mobile {
				top: -34px;
				background-size: 18%;
			}
    }
  }

  &__text {
    font-family: "Corbel", san-serif;
    color: var(--brownish-grey);
    font-size: 21px;
    text-align: left;
    display: block;
    line-height: 28px;
		width: 56%;
		margin: 0;
		margin-top: 28px;
		@include mobile {
			font-size: 14px;
			line-height: 20px;
		}
  }

  &__descr {
		font-family: 'Arial' , sans-serif;
    width: 80%;
    background-color: var(--color-black);
    position: absolute;
    left: -280px;
    bottom:84px;
		border-radius: 14px;
		z-index: 2;
		
		@media (max-width: (1701px)) {
			bottom: 24px;
		}
  }

  &__title {
    font-size: 21px;
    padding: 0;
    margin: 0;
    display: block;
    padding-top: 40px;
		padding-left: 105px;
		padding-bottom: 25px;
		text-transform: uppercase;
		@include mobile{
			font-size: 12px;
			padding:15px 0 5px 52px;
		}
  }

  &__item {
		float: left;
		display: block;
		width: 26%;
    margin: 0;
		margin-bottom: 29px;
	}
	&__items {
		position: relative;
		font-size: 20px;
		height: 68px;
		width: 86px;

    @include mobile { 
			height: 39px;
			width: 49px;
		}
	}
	
	&__option {
		width: 67%;
		display: inline-block;
		font-size: 21px;
		margin: 0;
		@include mobile { 
			font-size: 11px;
		}
	}
	&__options {
		&:first-child {
			padding-top: 23px;
			@include mobile {
				padding-top: 9px;
			}
		}
		&:not(:last-child) {
			padding-bottom: 15px;
			@include mobile {
				padding-bottom: 19px;
			}
		}
	}
	
	&__scooter {
      left: -1px;
			border-top-right-radius: 25px;
			background-image: url(../img/scooter.png);
			background-color: var(--greeny-blue);
			background-position-x: center;
			background-position-y: 29px;
      background-repeat: no-repeat;
      background-size: 51%;
			object-fit: cover;
			@include mobile{ 
				border-top-right-radius: 15px;
				background-position-y:15px;
			}
	}

	&__runningman {
      left: -1px;
			background-image: url(../img/running-men.png);
			background-color: var(--greeny-blue);
			background-position-x: center;
			background-position-y: 23px;
      background-repeat: no-repeat;
      background-size: 51%;
			object-fit: cover;
			@include mobile{ 
				background-position-y:15px;
			}
	}
	&__tea {
      left: -1px;
			background-image: url(../img/tea.png);
			background-color: var(--greeny-blue);
			background-position-x: center;
			background-position-y: 21px;
      background-repeat: no-repeat;
      background-size: 50%;
			object-fit: cover;
			@include mobile{ 
				background-position-y:15px;
			}
	}
	&__rocket {
      left: -1px;
			border-bottom-right-radius: 25px;
			background-image: url(../img/rocket.png);
			background-color: var(--greeny-blue);
			background-position-x: center;
			background-position-y: 7px;
      background-repeat: no-repeat;
      background-size: 44%;
			object-fit: cover;
			@include mobile{ 
				border-bottom-right-radius: 15px;
				background-position-y:15px;
			}
  
	}
}
