.stable {
  z-index: -1;
  padding-bottom: 100px;
  background-color: var(--color-white);
  padding-top: 135px;

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    // width: 76%;
    // padding: 0;
    padding-bottom: 45px;
  }

  &__caption {
    margin: 0;
    padding: 0;
    text-align: center;
  }

  &__text {
    margin: 16px 57px 24px 57px;
    font-family: Corbel;
    font-size: 21px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.36;
    letter-spacing: normal;
    text-align: center;
    color: var(--brownish-grey);
  }
}

.slider-stable {
  margin-top: 10px;
  display: block;
  position: relative;
  padding: 0px;

  &__item {
    text-align: center;
    padding: 20px 15px;
    height: 340px;

  }

  &__image {
    height: 100%;
    width: 100%;
    object-fit: cover;
    overflow: hidden;
  }

  &__text {
    margin-top: 32px;
    text-transform: uppercase;
    color: var(--almost-black);
    font-family: 'Corbel', sans-serif;
    font-size: 21px;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: normal;
    text-align: center;
    color: var(--almost-black);

    b {
      display: block;
      margin: 0 41px;
    }
  }

  .slick-arrow {
    position: absolute;
    top: 50%;
    z-index: 10;
    font-size: 0;
  }

  .slick-arrow.slick-prev {
    left: 0;
  }

  .slick-arrow.slick-next {
    right: 0;
  }


  .slick-track {
    display: flex;
  }

  .slick-list {
    overflow: hidden;
    margin-left: -5px;
    margin-right: -5px;
  }

  .slick-dots {
    bottom: -55px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .slick-dots li {
    list-style: none;
    margin: 0px 10px;
  }

  /* Активная точка */
  .slick-dots li.slick-active button {
    background-color: var(--almost-black);
    border: 2px solid #46bd97;
    cursor: pointer;
  }

  .slick-dots li button:before {
    content: '';
  }

  /* Элемент точки */
  .slick-dots button {
    font-size: 0;
    width: 16px;
    height: 16px;
    background-color: var(--brownish-grey-43);
    border-radius: 50%;
    cursor: pointer;
  }

  .slick-arrow.slick-disabled {
    opacity: 0.15;
  }

}
