.hunters-house {
  position: relative;
  display: block;
  min-height: 707px;
  padding: 108px 0 0 0px;

  // &::before {
  //   position: absolute;
  //   content: "";
  //   display: block;
  //   top: 0;
  //   left: 0;
  //   bottom: 0;
  //   right: 0;
  //   background-image: url("../img/house-black--lake.jpg");
  //   background-position: 0px -56px;
  //   background-repeat: no-repeat;
  //   background-size: cover;
  // background-attachment: fixed;
  // }

  &__container {
    position: relative;
    display: block;
    padding-bottom: 30px;
  }

  &__title {
    padding: 0 0 36px 0;
    margin: 0;
    text-align: left;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 421px;
    width: 70%;
    counter-reset: heading;
  }

  &::after {
    z-index: 100;
    position: absolute;
    content: "";
    right: 230px;
    bottom: 3px;
    height: 200px;
    width: 200px;
    background-image: url(../img/bird.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 80%;
    object-fit: cover;
  }

  &__items {
    position: relative;
    display: block;
    font-family: "Corbel", sans-serif;
    font-size: 35px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1;
    letter-spacing: normal;
    text-align: left;
    padding: 0 107px 35px 10px;
    margin-left: 43px;

    @include mobile {
      font-size: 20px;
    }

    &::before {
      position: absolute;
      left: -50px;
      top: 0;
      font-family: "Corbel", sans-serif;
      font-size: 31.5px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: left;
      color: var(--greeny-blue);
      counter-increment: heading;
      content: "0"counter(heading) " ";

      @include mobile {
        top: -10px;
      }
    }

    &:nth-child(n + 10)::before {
      content: " "counter(heading) " ";
    }

    &__items:nth-child(6n + 1) {
      clear: both;
    }
  }

  &__svg {
    z-index: 10;
    position: absolute;
    top: 447px;
    right: -98px;
    min-width: 484px;
    min-height: 270px;
  }
}
