.sauna {
  &__container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    // width: 76%;
    // padding: 0;mobile
  }

  &__caption {
    color: var(--greeny-blue);
    text-align: center;
    position: relative;
    margin-top: 153px;
    margin-bottom: 12px;

    &::after {
      z-index: 100;
      position: absolute;
      content: "";
      left: 152px;
      bottom: -17px;
      height: 100px;
      width: 240px;
      background-image: url(../img/wave.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 46%;
      object-fit: cover;
    }
  }

  &__text {
    margin: 27px auto;
    padding: 0px;
    font-family: Corbel;
    font-size: 21px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.36;
    letter-spacing: normal;
    text-align: center;
    color: var(--brownish-grey);
    width: 50%;

    @include mobile {
      width: 50%;
      font-size: 11px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: center;
    margin: 0;
    margin-bottom: 39px;
  }

  &__items {
    padding: 24px 0px 46px 0px;
    width: 27%;
    display: block;
    font-family: Corbel;
    font-size: 31px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.13;
    letter-spacing: normal;
    text-align: center;
    color: var(--almost-black);

    &:nth-child(3n-1) {
      margin: 0px 45px;
    }

    @include mobile {
      font-size: 20px;
      padding: 0px 0px 16px 0px;
    }
  }
}

.sauna__svg {
  margin: 0px auto;
  display: block;
  width: 23%;
  height: 130px;
}
