.gallary-forest {
  z-index: -1;
  margin-top: -235px;
  padding-bottom: 29px;
  background-color: var(--light-grey);

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    // width: 76%;
    // padding: 0;
  }

  &__slogan {
    font-family: "Arkhip", sans-serif;
    display: block;
    margin-top: 360px;
    padding: 0 100px 0;
    font-size: 36px;
    letter-spacing: -1.26px;
    color: var(--almost-black);
		align-self: center;
		
		@include mobile {
			font-size: 25px;
		}
  }
}

.slider {
  margin-top: 10px;
  position: relative;
  display: block;
  padding: 0 0px;

  &__item {
    // min-width: 490px;
    text-align: center;
    padding: 20px 15px;
		// width: 100%;
		opacity: 0.75;
		height: vw(345);

    .slick-center {
      opacity: 1;
    }
  }

  &__image {
		object-fit: cover;
		width: 100%;
	
    // object-fit: cover;
    // overflow: hidden;
  }

  .slick-arrow {
    position: absolute;
    top: 50%;
    z-index: 10;
    font-size: 0;
  }

  .slick-arrow.slick-prev {
    left: 0;
  }

  .slick-arrow.slick-next {
    right: 0;
  }


  .slick-track {
    display: flex;
  }

  .slick-list {
    overflow: hidden;
    margin-left: -5px;
    margin-right: -5px;
  }

  .slick-dots {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .slick-dots li {
    list-style: none;
    margin: 0px 10px;
  }

  /* Активная точка */
  .slick-dots li.slick-active button {
    background-color: transparent;
    border: 2px solid #46bd97;
    cursor: pointer;
  }

  .slick-dots li button:before {
    content: '';
  }

  /* Элемент точки */
  .slick-dots button {
    font-size: 0;
    width: 16px;
    height: 16px;
    background-color: var(--brownish-grey-43);
    border-radius: 50%;
    cursor: pointer;
  }

  .slick-arrow.slick-disabled {
    opacity: 0.15;
  }
}
