.extra {
  padding: 108px 0 0 0px;
  // background-image: url("../img/sauna.jpg");
  background-position: 0px 0px;
  background-repeat: no-repeat;
  background-size: cover;

  @include mobile {
    padding-top: 38px;
  }

  &__container {
    position: relative;
    flex-wrap: wrap;
    display: flex;
    // width: 76%;
    // padding: 0;
  }

  &__title {
    display: block;
    word-wrap: break-word;
    width: 16%;
    margin: 13px 266px 0px 0px;
    text-align: left;
    height: 300px;

    @include mobile {
      width: 14%;
      margin-bottom: 10px;
      margin-right: 45px;
      height: 80px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    // justify-content: space-around;
    flex-wrap: wrap;
    width: 65%;
    max-height: 569px;
    padding-top: 10px;
    margin-bottom: 147px;

    @include mobile {
      // flex-direction: row;
      justify-content: space-around;
      // width: 80%;
      max-height: 250px;
      margin-bottom: 107px;
    }
  }

  &__items {
    position: relative;
    display: block;
    padding: 0px 78px 70px 0px;
    width: 52%;
    margin: 0;
    margin-right: 48px;
    text-transform: uppercase;
    font-family: 'Corbel', sans-serif;
    font-size: 31px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.14;
    letter-spacing: normal;
    text-align: left;
    color: var(--color-white);

    @include mobile {
      width: 47%;
      font-size: 18px;
      padding: 0 22px 22px 0px;
      margin-right: 50px;
    }

    &::after {
      content: '';
      position: absolute;
      left: -56px;
      top: 0px;
      height: 44px;
      width: 41px;
      background-image: url(../img/svg/leaf.svg);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 102%;
      object-fit: cover;

      @include mobile {
        top: -18px;
        width: 28px;
        left: -40px;
      }


      &:nth-child(2) {
        // margin-left: 12px;
        order: 7;
        width: 85%;
      }

      &:nth-child(7) {
        order: 2;
      }
    }

    &:nth-child(4n) {
      white-space: nowrap;
    }

    &:nth-child(6n) {
      white-space: nowrap;
    }
  }



  &__svg {
    display: inline-block;
    z-index: 100;
    height: 41px;
    width: 44px;
    object-fit: cover;

    @include mobile {
      height: 31px;
      width: 34px;
    }
  }

  &__flower {
    position: absolute;
    bottom: 0;

    &_midi {
      height: 24.2%;
      left: 79px;
    }

    &_small {
      height: 27.5%;
      left: -12px;
    }

    &_bigi {
      height: 38%;
      left: 39px;
    }
  }

}
