.footer {
  position: relative;
  z-index: 1;
  background-color: var(--light-grey);

  &__wrapper {
    position: relative;

  }

  &__container {
    margin-top: 15px;
    display: flex;

    justify-content: flex-start;
    // width: 76%;
    // padding: 0;
  }

  &__main-image {
    position: absolute;
    top: -90px;
    left: 1144px;
  }

  &__logo {
    width: 30%;
    position: relative;
    display: inline-block;
    background-color: var(--light-grey);
		

    &::after {
      z-index: 100;
      position: absolute;
      content: "";
      left: 174px;
      top: 94px;
      height: 100px;
      width: 240px;
      background-image: url(../img/wave.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 29%;
      object-fit: cover;
    }

    &-text {
      letter-spacing: -3.9px;
      font-size: 71px;
      color: var(--greeny-blue);
      padding-top: 13px;
    }
  }

  &__svg {
    z-index: 100;
    position: absolute;
    right: 78px;
    top: 77px;
    height: 29px;
    width: 44px;
    object-fit: cover;
    transform: rotate(45deg);
  }

  &__address {
    margin-left: 57px;
    padding-top: 80px;
    width: 30%;
    display: block;
    margin-bottom: 70px;
  }

  &__call {
    font-family: 'Corbel', sans-serif;
    font-size: 41px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 0.5;
    letter-spacing: normal;
    text-align: left;
    color: var(--almost-black);
  }

  &__address-text {
    font-family: 'Corbel', sans-serif;
    font-size: 21px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: normal;
    text-align: left;
    color: var(--charcoal-grey);
    margin-bottom: 33px;
  }
}

.social-list {
  display: block;
  width: 100%;

  &__item {
    padding-right: 36px;
    padding-top: 36px;
    display: inline-block;
  }

  &__link {
    cursor: pointer;
  }

  &__svg {
    width: 49px;
    height: 49px;
  }
}
