.parallax {
  position: relative;
  height: 100%;
}

.burger {

  display: none;
  outline: none;
  cursor: pointer;
  background: transparent;
  // background-color: #fbfbfb;
  z-index: 99;

  @include tablet {
    padding-top: vmin(30);
    width: vmin(50);
    height: vmin(40);
    // height: vmin(122);
    display: block;
  }

  @include mobile {
    width: vmin(50);
  }

  svg {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    fill: #000;
  }
}

#nav-icon3 {
  display: block;
  position: relative;
  margin: 0px auto;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: transform .5s ease-in-out;
  cursor: pointer;
}

#nav-icon3 span {
  display: block;
  position: absolute;
  height: 6px;
  width: 99%;
  background: var(--almost-black);
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: transform .25s ease-in-out;

  @include mobile {
    height: 3px;
  }
}

#nav-icon3 span:nth-child(1) {
  top: -14px;
  background: var(--almost-black);

  @include mobile {
    top: -8px;
  }
}

#nav-icon3 span:nth-child(2),
#nav-icon3 span:nth-child(3) {
  top: -2px;
  background: var(--almost-black);

  @include mobile {
    top: 0px;
  }
}

#nav-icon3 span:nth-child(4) {
  top: 10px;
  background: var(--almost-black);
}

#nav-icon3.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

#nav-icon3.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

#nav-icon3.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

#nav-icon3.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.header {

  left: 0;
  top: 0;
  width: 100%;
  background-image: url(../img/fog.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
  z-index: 99;

  &__nav {
    display: flex;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    margin-bottom: 398px;
  }

  &__list {
    position: absolute;
    align-items: center;
    display: flex;

    @include tablet {
      display: none;
    }
  }

  &__item {
    margin-top: vw(21);
    display: inline-block;
    transition: 1s;

    &:not(:last-child) {
      margin-right: vw(21);
    }
  }

  &__link {
    font-size: vw(29);
    color: var(--charcoal-grey);
    transition: color 0.5s;


    &:hover {
      transition: 0.3s;
      color: var(--very-green);
    }
  }

  &__text {
    font-family: 'Corbel', sans-serif;
    align-self: center;
    top: 60%;
    left: 50%;
    font-size: vw(33);
    font-weight: normal;
    line-height: normal;
    text-align: center;
    margin: -12px 0 216px;


    @include tablet {
      font-size: vw(32);
      margin: -12px;
      top: 54%;
    }
  }

  &__parallax-fog {
    background: url('../img/fog_1.png') bottom / 100% no-repeat;
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 2;
    pointer-events: none;

    @include tablet {
      top: vw(-600);
    }
  }

  &__headline {
    display: flex;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    color: var(--color-white);
    font-family: "Arkhip", sans-serif;
    font-weight: normal;
    font-size: vw(201);
    line-height: 202px;
    letter-spacing: -9.19px;
    text-align: center;
    margin-bottom: 9px;


    @include tablet {
      font-size: vw(191);

    }
  }
}

.search-form {
  left: 50%;
  top: 100%;
  width: vw(1460);

  &__list {
    position: relative;
    top: vw(87);
    background-color: var(--color-white);
    padding: 60px 79px 57px 83px;
    display: flex;

    @include tablet {
      padding: vw(20) vw(29) vw(17) vw(29);
    }
  }

  &__item {
    width: 25%;
    height: auto;
    border: 2px solid var(--brownish-grey-43);
    text-align: center;
    vertical-align: middle;

    &:first-child {
      border-top-left-radius: 12px;
      border-bottom-left-radius: 12px;
    }

    &:last-child {
      background-color: var(--greeny-blue);
      border-top-right-radius: 12px;
      border-bottom-right-radius: 12px;
    }

    &:not(:first-child) {
      border-left: 0px;
    }
  }

  &__link {
    padding: vw(26) vw(0);
    display: inline-block;
    text-transform: uppercase;
    color: var(--brownish-grey-43);
    font-size: vw(23.5);
    font-weight: bold;

    @include tablet {
      font-size: vw(17.5);
      padding: vw(18) 0;
    }
  }

  &__btn {
    color: var(--color-white);
    background-color: transparent;
  }
}
