.location {
  width: 100%;
  background-color: var(--color-white);
  color: var(--greeny-blue);
  @include flex-all-center;

  &__info-address {
    align-self: flex-start;
    flex-shrink: 0;
    width: 560px;
    padding-left: max(15px, calc((100% - var(--content-width)) / 2));
  }

  &__title {
    font-size: 1.6em;
    margin: 0px;
    text-align: left;
  }

  &__map {
    border-left: 2px solid var(--greeny-blue);
    z-index: 10;
    width: calc(100% - 560px - var(--container-offset));
    height: 556px;
  }

  &__text-address {
    margin: 0px;
    padding: 0px;
    font-family: Corbel;
    font-size: 21px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.36;
    letter-spacing: normal;
    text-align: center;
    color: var(--brownish-grey);
  }

  &__text-transport {
    margin: 0px;
    padding: 0px;
    font-family: Corbel;
    font-size: 18px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.26;
    letter-spacing: normal;
    text-align: left;
    color: var(--brownish-grey);
  }
}

[class*="copyrights-pane"] {
  display: none !important;
}
